"use strict";
import { create } from "zustand";
import { devtools } from "zustand/middleware";
export const usePriceRange = create()(
  devtools(
    (set) => ({
      priceRangeLow: "",
      setPriceRangeLow: (priceRangeLow) => {
        set(() => {
          return { priceRangeLow };
        });
      },
      priceRangeHigh: "",
      setPriceRangeHigh: (priceRangeHigh) => {
        set(() => {
          return { priceRangeHigh };
        });
      },
      prevMinMax: [0, 100],
      setPrevMinMax: (prevMinMax) => {
        set(() => {
          return { prevMinMax };
        });
      }
    }),
    { name: "usePriceRange" }
  )
);
