"use strict";
import { create } from "zustand";
import { devtools } from "zustand/middleware";
export const useIsCollectionLoading = create()(
  devtools(
    (set) => ({
      isCollectionNftsLoading: false,
      setIsCollectionNftsLoading: (isCollectionNftsLoading) => set(() => {
        return { isCollectionNftsLoading };
      }),
      isCollectionStatsLoading: false,
      setIsCollectionStatsLoading: (isCollectionStatsLoading) => set(() => {
        return { isCollectionStatsLoading };
      })
    }),
    { name: "useIsCollectionLoading" }
  )
);
