"use strict";
import { NFTEventName, NFTFilterTypes } from "@uniswap/analytics-events";
import clsx from "clsx";
import { Box } from "components/deprecated/Box";
import styled from "lib/styled-components";
import { Column, Row } from "nft/components/Flex";
import * as styles from "nft/components/collection/Filters.css";
import { ChevronUpIcon } from "nft/components/icons";
import { subheadSmall } from "nft/css/common.css";
import { useCollectionFilters } from "nft/hooks/useCollectionFilters";
import { TraitPosition, useTraitsOpen } from "nft/hooks/useTraitsOpen";
import { getMarketplaceIcon } from "nft/utils";
import { useEffect, useMemo, useState } from "react";
import { ThemedText } from "theme/components";
import { LabeledCheckbox } from "ui/src";
import { sendAnalyticsEvent } from "uniswap/src/features/telemetry/send";
const FilterItemWrapper = styled(Row)`
  justify-content: space-between;
  padding: 10px 16px 10px 12px;
  cursor: pointer;
  border-radius: 12px;
  &:hover {
    background: ${({ theme }) => theme.surface2};
  }
`;
const MarketNameWrapper = styled(Row)`
  gap: 10px;
`;
export const MARKETPLACE_ITEMS = {
  x2y2: "X2Y2",
  opensea: "OpenSea",
  looksrare: "LooksRare",
  sudoswap: "SudoSwap",
  nftx: "NFTX",
  nft20: "NFT20",
  cryptopunks: "LarvaLabs"
};
export const FilterItem = ({
  title,
  element,
  onClick
}) => {
  return <FilterItemWrapper onClick={onClick}><ThemedText.BodyPrimary>{title}</ThemedText.BodyPrimary><ThemedText.SubHeaderSmall>{element}</ThemedText.SubHeaderSmall></FilterItemWrapper>;
};
const MarketplaceItem = ({
  title,
  value,
  addMarket,
  removeMarket,
  isMarketSelected,
  count
}) => {
  const [isCheckboxSelected, setCheckboxSelected] = useState(false);
  useEffect(() => {
    setCheckboxSelected(isMarketSelected);
  }, [isMarketSelected]);
  const handleCheckbox = () => {
    if (!isCheckboxSelected) {
      addMarket(value);
      setCheckboxSelected(true);
    } else {
      removeMarket(value);
      setCheckboxSelected(false);
    }
    sendAnalyticsEvent(NFTEventName.NFT_FILTER_SELECTED, { filter_type: NFTFilterTypes.MARKETPLACE });
  };
  const checkbox = <LabeledCheckbox
    variant="branded"
    checked={isCheckboxSelected}
    onCheckPressed={handleCheckbox}
    text={String(count)}
  />;
  const titleWithLogo = <MarketNameWrapper>{getMarketplaceIcon(title, "16")}{title}</MarketNameWrapper>;
  return <div key={value}><FilterItem title={titleWithLogo} element={checkbox} onClick={handleCheckbox} /></div>;
};
export const FilterDropdown = ({
  title,
  items,
  onClick,
  isOpen
}) => {
  return <><Box className={styles.detailsOpen} opacity={isOpen ? "1" : "0"} /><Box
    as="details"
    className={clsx(subheadSmall, !isOpen && styles.rowHover)}
    open={isOpen}
    borderRadius={isOpen ? "0" : "12"}
  ><Box
    as="summary"
    className={`${styles.row} ${styles.rowHover}`}
    display="flex"
    justifyContent="space-between"
    alignItems="center"
    fontSize="16"
    paddingTop="12"
    paddingLeft="12"
    paddingBottom="12"
    lineHeight="20"
    borderRadius="12"
    maxHeight="48"
    onClick={onClick}
  >{title}<Box display="flex" alignItems="center"><Box
    className={styles.chevronContainer}
    style={{
      transform: `rotate(${isOpen ? 0 : 180}deg)`
    }}
  ><ChevronUpIcon className={styles.chevronIcon} /></Box></Box></Box><Column className={styles.filterDropDowns} paddingBottom="8" paddingLeft="0">{items}</Column></Box></>;
};
export const MarketplaceSelect = () => {
  const {
    addMarket,
    removeMarket,
    markets: selectedMarkets,
    marketCount
  } = useCollectionFilters(({ markets, marketCount: marketCount2, removeMarket: removeMarket2, addMarket: addMarket2 }) => ({
    markets,
    marketCount: marketCount2,
    removeMarket: removeMarket2,
    addMarket: addMarket2
  }));
  const [isOpen, setOpen] = useState(!!selectedMarkets.length);
  const setTraitsOpen = useTraitsOpen((state) => state.setTraitsOpen);
  const MarketplaceItems = useMemo(
    () => Object.entries(MARKETPLACE_ITEMS).map(([value, title]) => <MarketplaceItem
      key={value}
      title={title}
      value={value}
      count={marketCount?.[value] || 0}
      {...{ addMarket, removeMarket, isMarketSelected: selectedMarkets.includes(value) }}
    />),
    [addMarket, marketCount, removeMarket, selectedMarkets]
  );
  const onClick = (e) => {
    e.preventDefault();
    setOpen(!isOpen);
    setTraitsOpen(TraitPosition.MARKPLACE_INDEX, !isOpen);
  };
  return <FilterDropdown title="Marketplaces" items={MarketplaceItems} onClick={onClick} isOpen={isOpen} />;
};
