"use strict";
import { NFTEventName, NFTFilterTypes } from "@uniswap/analytics-events";
import { Box } from "components/deprecated/Box";
import useDebounce from "hooks/useDebounce";
import { Column, Row } from "nft/components/Flex";
import * as styles from "nft/components/collection/Filters.css";
import { TraitsHeader } from "nft/components/collection/TraitsHeader";
import { Input } from "nft/components/layout/Input";
import { subheadSmall } from "nft/css/common.css";
import { useCollectionFilters } from "nft/hooks/useCollectionFilters";
import { pluralize } from "nft/utils/roundAndPluralize";
import { scrollToTop } from "nft/utils/scrollToTop";
import { useCallback, useEffect, useMemo, useState } from "react";
import AutoSizer from "react-virtualized-auto-sizer";
import { FixedSizeList } from "react-window";
import { LabeledCheckbox } from "ui/src";
import { sendAnalyticsEvent } from "uniswap/src/features/telemetry/send";
const TRAIT_ROW_HEIGHT = 44;
const TraitItem = ({
  trait,
  addTrait,
  removeTrait,
  isTraitSelected,
  style
}) => {
  const [isCheckboxSelected, setCheckboxSelected] = useState(false);
  const [hovered, setHovered] = useState(false);
  const handleHover = () => setHovered(!hovered);
  const toggleShowFullTraitName = useCollectionFilters((state) => state.toggleShowFullTraitName);
  const { shouldShow, trait_value, trait_type } = useCollectionFilters((state) => state.showFullTraitName);
  const isEllipsisActive = (e) => {
    if (e.currentTarget.offsetWidth < e.currentTarget.scrollWidth) {
      toggleShowFullTraitName({
        shouldShow: true,
        trait_value: trait.trait_value,
        trait_type: trait.trait_type
      });
    }
  };
  useEffect(() => {
    setCheckboxSelected(isTraitSelected);
  }, [isTraitSelected]);
  const handleCheckbox = () => {
    scrollToTop();
    if (!isCheckboxSelected) {
      addTrait(trait);
      setCheckboxSelected(true);
    } else {
      removeTrait(trait);
      setCheckboxSelected(false);
    }
    sendAnalyticsEvent(NFTEventName.NFT_FILTER_SELECTED, { filter_type: NFTFilterTypes.TRAIT });
  };
  const showFullTraitName = shouldShow && trait_type === trait.trait_type && trait_value === trait.trait_value;
  return <Row
    key={trait.trait_value}
    maxWidth="full"
    overflowX="hidden"
    overflowY="hidden"
    fontWeight="book"
    className={`${subheadSmall} ${styles.subRowHover}`}
    justifyContent="space-between"
    cursor="pointer"
    paddingLeft="12"
    paddingRight="16"
    borderRadius="12"
    style={{
      paddingBottom: "22px",
      paddingTop: "22px",
      ...style
    }}
    maxHeight="44"
    onMouseEnter={handleHover}
    onMouseLeave={handleHover}
    onClick={handleCheckbox}
  ><Box
    as="span"
    whiteSpace="nowrap"
    textOverflow="ellipsis"
    overflow="hidden"
    style={{ minHeight: 15 }}
    maxWidth={!showFullTraitName ? "160" : "full"}
    onMouseOver={(e) => isEllipsisActive(e)}
    onMouseLeave={() => toggleShowFullTraitName({ shouldShow: false, trait_type: "", trait_value: "" })}
  >{trait.trait_type === "Number of traits" ? `${trait.trait_value} trait${pluralize(Number(trait.trait_value))}` : trait.trait_value}</Box><LabeledCheckbox
    checked={isCheckboxSelected}
    onCheckPressed={handleCheckbox}
    text={showFullTraitName ? String(trait.trait_count) : ""}
    variant="branded"
  /></Row>;
};
export const TraitSelect = ({ traits, type, index }) => {
  const addTrait = useCollectionFilters((state) => state.addTrait);
  const removeTrait = useCollectionFilters((state) => state.removeTrait);
  const selectedTraits = useCollectionFilters((state) => state.traits);
  const [search, setSearch] = useState("");
  const debouncedSearch = useDebounce(search, 300);
  const searchedTraits = useMemo(
    () => traits.filter((t) => t.trait_value?.toString().toLowerCase().includes(debouncedSearch.toLowerCase())),
    [debouncedSearch, traits]
  );
  const Row2 = useCallback(
    function TraitRow({ data, index: index2, style }) {
      const trait = data[index2];
      const isTraitSelected = selectedTraits.find(
        ({ trait_type, trait_value }) => trait_type === trait.trait_type && String(trait_value) === String(trait.trait_value)
      );
      return <TraitItem style={style} isTraitSelected={!!isTraitSelected} {...{ trait, addTrait, removeTrait }} />;
    },
    [selectedTraits, addTrait, removeTrait]
  );
  const itemKey = useCallback((index2, data) => {
    const trait = data[index2];
    return `${trait.trait_type}_${trait.trait_value}_${index2}`;
  }, []);
  return traits.length ? <TraitsHeader index={index} numTraits={traits.length} title={type}><Input
    value={search}
    onChange={(e) => setSearch(e.currentTarget.value)}
    placeholder="Search"
    marginTop="8"
    marginBottom="8"
    autoComplete="off"
    position="static"
    width="full"
  /><Column
    className={styles.filterDropDowns}
    style={{
      height: `${Math.min(TRAIT_ROW_HEIGHT * searchedTraits.length, styles.MAX_FILTER_DROPDOWN_HEIGHT)}px`
    }}
  ><AutoSizer disableWidth>{({ height }) => <FixedSizeList
    height={height}
    width="100%"
    itemData={searchedTraits}
    itemCount={searchedTraits.length}
    itemSize={TRAIT_ROW_HEIGHT}
    itemKey={itemKey}
  >{({ index: index2, style, data }) => <Row2 style={style} key={itemKey(index2, data)} data={data} index={index2} />}</FixedSizeList>}</AutoSizer></Column></TraitsHeader> : null;
};
