"use strict";
import styled from "lib/styled-components";
import { FilterDropdown, FilterItem } from "nft/components/collection/MarketplaceSelect";
import { useCollectionFilters } from "nft/hooks";
import { useState } from "react";
import { Check } from "react-feather";
const CheckIcon = styled(Check)`
  height: 20px;
  width: 20px;
  color: ${({ theme }) => theme.accent1};
`;
export const FilterSortDropdown = ({ sortDropDownOptions }) => {
  const [isOpen, setOpen] = useState(false);
  const onClick = (e) => {
    e.preventDefault();
    setOpen(!isOpen);
  };
  const sortItems = sortDropDownOptions.map((option) => <SortByItem dropDownOption={option} parentOnClick={onClick} key={option.displayText} />);
  return <FilterDropdown title="Sort by" items={sortItems} onClick={onClick} isOpen={isOpen} />;
};
const SortByItem = ({
  dropDownOption,
  parentOnClick
}) => {
  const sortBy = useCollectionFilters((state) => state.sortBy);
  const checkMark = dropDownOption.sortBy !== void 0 && sortBy === dropDownOption.sortBy ? <CheckIcon /> : <></>;
  const onClick = (e) => {
    e.preventDefault();
    parentOnClick(e);
    dropDownOption.onClick();
  };
  return <FilterItem title={dropDownOption.displayText} element={checkMark} onClick={onClick} />;
};
